import { ListGroup, Image } from "react-bootstrap";
import "./index.scss";
interface Mess {
  name: string;
  date: string;
  content: string;
}
interface RecentMess {
  recentMessages: Array<Mess>;
}
function RecentMessage({ recentMessages }: RecentMess) {
  return (
    <div className="recent-message">
      <h5 className="title">Recent Message</h5>
      <ListGroup variant="flush">
        {recentMessages &&
          recentMessages.map((mess) => (
            <ListGroup.Item action>
              <div className="list-item d-flex">
                <div className="flex-grow-0 avatar">
                  <Image src="./img/0.png" width="44" height="44" />
                </div>
                <div className="flex-grow-1 ml-2">
                  <div className="d-flex justify-content-between">
                    <h6 className="name">{mess.name}</h6>
                    <span className="text-secondary recent-date">
                      {mess.date}
                    </span>
                  </div>
                  <p className="text-secondary">{mess.content}</p>
                </div>
              </div>
            </ListGroup.Item>
          ))}
      </ListGroup>
    </div>
  );
}

export default RecentMessage;
