import { ListGroup, ProgressBar } from "react-bootstrap";
import "./index.scss";

function ProjectsProgress() {
  const projectsProgress = [
    {
      title: "Lorem 123456",
      time: 10,
      percent: 20,
    },
    {
      title: "Lorem 123456",
      time: 15,
      percent: 50,
    },
    {
      title: "Lorem 123456",
      time: 5,
      percent: 70,
    },
  ];
  return (
    <div className="project">
      <h5 className="title">Projects in progress</h5>
      <div className="project-bar">
        {projectsProgress &&
          projectsProgress.map((item) => (
            <div className="project-detail">
              <div className="project-title d-flex justify-content-between">
                <span>{item.title}</span>
                <span>{item.time} days to deadline</span>
              </div>
              <ProgressBar
                variant={
                  item.percent < 50
                    ? "success"
                    : item.percent < 70
                    ? "warning"
                    : "danger"
                }
                now={item.percent}
              />
            </div>
          ))}
      </div>
    </div>
  );
}

export default ProjectsProgress;
