import { ListGroup, Image } from "react-bootstrap";
import "./index.scss";
interface Connect {
  name: string;
  content: string;
}
interface IRecentConnect {
  recentConnects: Array<Connect>;
}
function RecentConnect({ recentConnects }: IRecentConnect) {
  return (
    <div className="recent-connect">
      <h5 className="title">Recent Connect</h5>
      <ListGroup variant="flush">
        {recentConnects &&
          recentConnects.map((connect) => (
            <ListGroup.Item action>
              <div className="list-item d-flex">
                <div className="flex-grow-0 avatar">
                  <Image src="./img/0.png" width="44" height="44" />
                </div>
                <div className="flex-grow-1 ml-2">
                  <h6 className="name">{connect.name}</h6>
                  <p className="text-secondary">{connect.content}</p>
                </div>
              </div>
            </ListGroup.Item>
          ))}
      </ListGroup>
    </div>
  );
}

export default RecentConnect;
