import { useState } from "react";
import { Button, Form, FormControl, Image } from "react-bootstrap";
import "./index.scss";
function Search({ search }: { search: Function }) {
  const [valueSearch, setValueSearch] = useState("");
  const getValueSearch = function (e: any) {
    search(e.target.value);
  };

  return (
    <div className="search-box">
      <Form className="search d-flex position-relative">
        <FormControl type="search" placeholder="Search" aria-label="Search" onChange={getValueSearch} />
        <Button type="submit" className="search-btn position-absolute " variant="">
          <Image src="./img/search.svg" />
        </Button>
      </Form>
    </div>
  );
}

export default Search;
