import { Image, ListGroup } from "react-bootstrap";
import "./index.scss";
interface item {
  title: string;
  author: string;
  status: string;
  date: string;
}
interface ListData {
  list: Array<item>;
}
function DashboardLeft({ list }: ListData) {
  return (
    <div className="dashboard-left ">
      <ListGroup variant="flush">
        {list &&
          list.map((item) => (
            <ListGroup.Item action className="d-flex ">
              <Image src="./img/0.png" width="50px" height="50px" roundedCircle />
              <div className="d-flex justify-content-between w-100 ml-3">
                <div className="title">
                  <h6> {item.title} </h6>
                  <div className="author text-secondary"> {item.author} </div>
                </div>
                <div>
                  <div className="status text-success text-right font-weight-bold"> {item.status} </div>
                  <div className="date text-secondary"> {item.date} </div>
                </div>
              </div>
            </ListGroup.Item>
          ))}
      </ListGroup>
    </div>
  );
}

export default DashboardLeft;
