import { useState } from "react";
import { Button, Pagination, Table } from "react-bootstrap";
import Search from "../../components/seach-box";
import "./index.scss";

function Projects() {
  const postPerPage = 5;
  const columns = [
    "Status",
    "Project Name",
    "Project ID",
    "Partner",
    "Start Date",
    "End Date",
  ];
  const listProjects: Array<any> = [];
  for (let i = 0; i <= 20; i++) {
    listProjects.push({
      status: "Active",
      name: "Medical Device " + i,
      ID: i,
      partner: "Partner " + i,
      start: new Date().toLocaleDateString(),
      end: new Date().toLocaleDateString(),
    });
  }

  //
  const [projects, setProject] = useState(listProjects);
  let [currentPage, setCurrentPage] = useState(1);

  //
  const indexOfFirstPost = (currentPage - 1) * postPerPage;
  const indexOfLastPost = currentPage * postPerPage;
  const currentPosts = projects.slice(indexOfFirstPost, indexOfLastPost);

  const rows = currentPosts.map((post, index) => (
    <tr key={index}>
      {Object.values(post).map((value, index) => (
        <td key={index}> {value} </td>
      ))}
    </tr>
  ));

  // pagination
  const totalPage = Math.ceil(projects.length / postPerPage);
  let items = [];
  for (let number = 1; number <= totalPage; number++) {
    items.push(
      <Pagination.Item
        key={number}
        active={number === currentPage}
        onClick={() => {
          setCurrentPage(number);
        }}
      >
        {number}
      </Pagination.Item>
    );
  }

  const prenext = (number: number) => {
    if (currentPage > 1 && number === -1) {
      setCurrentPage(currentPage + number);
    }
    if (currentPage < totalPage && number === 1) {
      setCurrentPage(currentPage + number);
    }
  };

  const search = (value: any) => {
    if (!value) {
      setProject(listProjects);
      return;
    }
    const newList = listProjects.filter(
      (item) =>
        item.status.includes(value) ||
        item.name.includes(value) ||
        item.partner.includes(value) ||
        item.start.includes(value) ||
        item.end.includes(value)
    );
    setProject(newList);
  };

  return (
    <div className="projects">
      <div className="top-content d-flex flex-column flex-sm-row align-items-end align-items-sm-stretch">
        <div className="mr-auto mw-100 mb-md-0 mb-1">
          <Search search={search} />
        </div>

        <Button className="btn-create" variant="primary">
          New Project
        </Button>
      </div>

      <div className="projects-content">
        <div className="table p-md-4 pr-md-5 p-0">
          <Table responsive="lg">
            <thead>
              <tr>
                {columns.map((column, index) => (
                  <th key={index}>{column}</th>
                ))}
              </tr>
            </thead>
            <tbody>{rows}</tbody>
          </Table>
        </div>
        <div className="paginate d-flex">
          <div className=" m-auto">
            <Pagination>
              <Button
                variant="text"
                onClick={() => {
                  prenext(-1);
                }}
              >
                Pre
              </Button>
              {items}
              <Button
                variant="text"
                onClick={() => {
                  prenext(1);
                }}
              >
                Next
              </Button>
            </Pagination>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Projects;
